@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap');

/* partie header */
.header {
  position: absolute;
  z-index: 99;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  background-color: #fff;
  max-width: 100%;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
}
.title {
  margin: 15px;
  font-family: 'Open Sans';
  font-weight: bold;
  background: linear-gradient(to right, #00aaff 10%,#fff 50%, #FF9933 60%);
  -webkit-background-size : 200% auto;
  background-size: 200% auto;
  background-clip: border-box;
  color : #fff;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  display: inline-block;
}

@media screen and (max-width: 695px) {    
  .title{
      font-size: 20px;
  }
}
/*texte lien*/

.navigate ul {
  align-items: center;
  justify-content: center;
  list-style-type: none;
  padding: 0;
  display: flex;
  gap: 20px;
  margin-right: 20px;
}

.navigate li {
  position: relative;
}

.navigate a {
  text-decoration: none;
  font-family: 'Lato';
  color: #333;
  font-size: 20px;
  font-weight: 700;
  transition: color 0.3s ease;
}

.navigate a:hover,
.navigate a.active {
  color: #ff9933;
}

.navigate a::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: -5px;
  left: 0;
  background-color: #ff9933;
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.navigate a:hover::after,
.navigate a.active::after {
  transform: scaleX(1);
}

/* partie body */
.body{
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #fff;
}


/*partie titre*/
.titre {
  font-size: 3em;
  font-family: 'Lato';
  background: linear-gradient(to right, #22BBEA, #FF9933);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 400% 100%;
  animation: gradientAnimation 5s linear infinite;
}

@keyframes gradientAnimation {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

/*aide*/
.aide {
  border: 2px solid transparent;
  padding-left: 1rem;
  padding-right: 1rem;
  background-color: #fff;
  transition: border 0.6s ease;
}
.aide:hover {
  border-image: linear-gradient(to right, #22BBEA, #FF9933) 1;
  color: #000;
  background-color: #fff;
}
.aide h3 {
  font-weight: 300;
}
.aide h3:hover {
  font-weight: bold;
}

/*photo*/
.calendar img {
  border-image: linear-gradient(to right, #22BBEA, #FF9933) 1;
  animation: pulse 2s infinite; /* Animation infinie */
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.03); }
  100% { transform: scale(1); }
}



